.nav-toggler {
  position: absolute;
  left: -100%;
  top: -100%; }
  .nav-toggler:focus ~ .toggle-container .button-toggle {
    box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1); }

.nav-toggler:checked ~ .nav-toggler__container .nav-toggler__button:hover {
  box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1), 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1); }

.nav-toggler:checked ~ .nav-toggler__container .nav-toggler__button:before {
  -webkit-transform: translateY(-50%) rotate(45deg) scale(1);
          transform: translateY(-50%) rotate(45deg) scale(1); }

.nav-toggler:checked ~ .nav-toggler__container .nav-toggler__button:after {
  -webkit-transform: translateY(-50%) rotate(-45deg) scale(1);
          transform: translateY(-50%) rotate(-45deg) scale(1); }

.nav-toggler:checked:focus ~ .nav-toggler__container nav-toggler__button {
  box-shadow: 0 0 0 550px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1), 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1); }

.nav-toggler:checked ~ .nav {
  margin-bottom: 100px;
  pointer-events: auto;
  -webkit-transform: translate(50px, 50px);
          transform: translate(50px, 50px); }
  .nav-toggler:checked ~ .nav .nav-item {
    color: #000000;
    letter-spacing: 0;
    height: 40px;
    line-height: 40px;
    margin-top: 0;
    opacity: 1;
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    transition: 0.5s, opacity 0.1s; }
    .nav-toggler:checked ~ .nav .nav-item:nth-child(1) {
      transition-delay: 0.15s; }
      .nav-toggler:checked ~ .nav .nav-item:nth-child(1):before {
        transition-delay: 0.15s; }
    .nav-toggler:checked ~ .nav .nav-item:nth-child(2) {
      transition-delay: 0.1s; }
      .nav-toggler:checked ~ .nav .nav-item:nth-child(2):before {
        transition-delay: 0.1s; }
    .nav-toggler:checked ~ .nav .nav-item:nth-child(3) {
      transition-delay: 0.05s; }
      .nav-toggler:checked ~ .nav .nav-item:nth-child(3):before {
        transition-delay: 0.05s; }
    .nav-toggler:checked ~ .nav .nav-item:nth-child(4) {
      transition-delay: 0s; }
      .nav-toggler:checked ~ .nav .nav-item:nth-child(4):before {
        transition-delay: 0s; }
    .nav-toggler:checked ~ .nav .nav-item:before {
      opacity: 0; }

/* Toggle button */
.nav-toggler__button {
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 25px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  border-radius: 100%;
  transition: 0.6s; }
  .nav-toggler__button:hover {
    box-shadow: 0 0 0 8px rgba(0, 0, 0, 0.1), inset 0 0 0 20px rgba(0, 0, 0, 0.1); }
  .nav-toggler__button:before, .nav-toggler__button:after {
    position: absolute;
    content: '';
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #000000;
    border-radius: 5px;
    transition: 0.5s; }
  .nav-toggler__button:before {
    -webkit-transform: translateY(-50%) rotate(45deg) scale(0);
            transform: translateY(-50%) rotate(45deg) scale(0); }
  .nav-toggler__button:after {
    -webkit-transform: translateY(-50%) rotate(-45deg) scale(0);
            transform: translateY(-50%) rotate(-45deg) scale(0); }

.nav {
  display: inline-block;
  margin: 25px 25px 20px;
  pointer-events: none;
  transition: 0.5s; }

.nav-item {
  position: relative;
  display: inline-block;
  float: left;
  clear: both;
  color: transparent;
  font-size: 14px;
  letter-spacing: -6.2px;
  height: 7px;
  line-height: 7px;
  text-transform: uppercase;
  white-space: nowrap;
  -webkit-transform: scaleY(0.2);
          transform: scaleY(0.2);
  transition: 0.5s, opacity 1s; }
  .nav-item:nth-child(1) {
    transition-delay: 0s; }
    .nav-item:nth-child(1):before {
      transition-delay: 0s; }
  .nav-item:nth-child(2) {
    transition-delay: 0.05s; }
    .nav-item:nth-child(2):before {
      transition-delay: 0.05s; }
  .nav-item:nth-child(3) {
    transition-delay: 0.1s; }
    .nav-item:nth-child(3):before {
      transition-delay: 0.1s; }
  .nav-item:nth-child(4) {
    transition-delay: 0.15s; }
    .nav-item:nth-child(4):before {
      transition-delay: 0.15s; }
  .nav-item:nth-child(1) {
    letter-spacing: -7.8px; }
  .nav-item:nth-child(2) {
    letter-spacing: -6.4px; }
  .nav-item:nth-child(n + 4) {
    letter-spacing: -8px;
    margin-top: -7px;
    opacity: 0; }
  .nav-item:before {
    position: absolute;
    content: '';
    top: 50%;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #000000;
    -webkit-transform: translateY(-50%) scaleY(5);
            transform: translateY(-50%) scaleY(5);
    transition: 0.5s; }

